<template>
  <el-container class="myhospital dailylistingIn">
    <el-header style="height: 35px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>*振</span>
        <span style="margin:10px">0005674</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
    </el-header>
    <el-main>
      <div class="block">
        <el-pagination layout="prev, pager, next" :total="60"></el-pagination>
      </div>

      <el-card class="box-card" style="padding: 5px 0;">
        <el-row>
          <el-col :span="8">
            <div class="grid-content bg-purple"></div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <p style="color:#FF7800;font-size:22px;">
                ￥
                <span>{{}}</span>
              </p>
              <p class="myhospital_time_ft">预交金余额</p>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple myhospital_time">
              住院中，第
              <span>{{clientTime1}}</span>天
            </div>
          </el-col>
        </el-row>
        <el-divider class="myhospital_hr"></el-divider>
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark dailylistingIn_time">
              <span>{{clientTime}}入院</span>
            </div>
          </el-col>
        </el-row>
      </el-card>
      <!--  -->
      <div class="title" style="margin-top:10px">
        <div class="title-img"></div>
        <div class="title-font">
          <span>基本信息</span>
        </div>
      </div>
      <el-card class="box-card myhospital_LIST">
        <div>
          <el-row>
            <el-col :span="8" class="myhospital_list_left">
              <div class="grid-content bg-purple">患者姓名</div>
              <div class="grid-content bg-purple">科室</div>
              <div class="grid-content bg-purple">医生</div>
              <div class="grid-content bg-purple">流水号</div>
            </el-col>
            <el-col :span="16" class="myhospital_list_right">
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <div class="title" style="margin-top:10px">
        <div class="title-img"></div>
        <div class="title-font">
          <span>费用明细</span>
        </div>
      </div>
      <!--  -->
      <el-card class="box-card payz dailylistingIn_tab">
        <el-table 
          :show-header="false"
          :data="tableData"
          show-summary
          :summary-method="getSummaries"
          style="font-size: 13px;color:#333;"
        >
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column prop="sum" label="金额(元)" align="right"></el-table-column>
        </el-table>
      </el-card>
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
export default {
  data() {
    return {
      clientName: "刘溜",
      clientAge: "511",
      clientRoom: "00024578",
      clientTime: "2020-4-13",
      input: "",
      clientTime1: "3",
      //
      tableData: [
        {
          name: "床位费",
          sum:  40.00
        },
        {
          name: "中成药费",
          sum: 40.001
        },
        {
          name: "床位费",
          sum:  40.00
        },
        {
          name: "床位费",
          sum:  40.00
        }
      ]
    };
  },
  methods: {
    // 金额列求和
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总金额：";
        } else if (index === 1) {
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += "元";
          } else {
            sums[index] = "N/A";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  }
};
</script>



